<template>
	<div>
		<top></top>
		<div class="con">
			<p class="ap">
				<span></span>
			</p>
		</div>
		<div class="contain">
			
			<Form ref="formItem" :model="formItem" :rules="ruleForm" :label-width="70">
				<FormItem label="" prop="pName">
					<Input type="text" v-model="formItem.pName" placeholder=""></Input>
				</FormItem>
				<FormItem label="" prop="pIdNum">
					<Input type="text" v-model="formItem.pIdNum" placeholder=""></Input>
				</FormItem>

				<FormItem label="" prop="pAge">
					<Input type="text" v-model="formItem.pAge" placeholder=""></Input>
				</FormItem>
				<FormItem label="" prop="pSex">
					<Select v-model="formItem.pSex">
						<Option v-for="item in sexs" :value="item.value" :key="item.value">{{ item.value }}</Option>
					</Select>
				</FormItem>
				<FormItem label="" prop="pDept">
					<Select v-model="formItem.pDept">
						<Option v-for="item in department" :value="item.dName" :key="item.dName">{{ item.dName }}</Option>
					</Select>
				</FormItem>
				<FormItem label="">
					<i-switch v-model="formItem.ifMedical">
						<span slot="open"></span>
						<span slot="close"></span>
					</i-switch>
				</FormItem>
				<FormItem label="">
					<Input v-model="formItem.diseaseDescription" type="textarea" :autosize="{minRows: 6,maxRows: 6}" placeholder=""></Input>
				</FormItem>
				<FormItem>
					<Button style="float:right" type="primary" @click="submit('formItem')"> </Button>
				</FormItem>
			</Form>
		</div>
	</div>
</template>

<script>
import top from './../components/top.vue'
import cookie from './../tools/cookie.js'
import url from './../tools/config.js'
import axios from 'axios'
export default {
	name: 'index',
	data () {
		return {
			formItem:{
				pName:'',
				pIdNum:'',
				pSex:'',
				pAge:'',
				pDept:'',
				diseaseDescription:'',
				ifMedical:''
			},
			ruleForm:{
				pName: [
				{ required: true, message: '', trigger: 'blur' }
				],
				pIdNum: [
				{ required: true, message: '', trigger: 'blur' }
				],
				pAge:[
				{ required: true, message: '', trigger: 'blur' }
				],
				pSex: [
				{ required: true, message: '', trigger: 'blur' }
				],
				pDept:[
				{ required: true, message: '', trigger: 'blur' }
				]
			},
			sexs:[
			{
				value:''
			},
			{
				value:''
			},
			],
			department:[]
		}
	},
	methods: {
		submit(name) {
			this.$refs[name].validate((valid) => {
				if (valid) {
					axios.post(`${url}/register`,this.formItem)
					.then((result)=>{
						var res=result.data;
						if (res.status==1) {
							cookie.setCookie('hasGH',1,1);
							cookie.setCookie('pCardNum',res.result.pCardNum,1);
							this.$router.push({path:'/information'})
						}else {
							console.log(res.msg)
						}
					})
					.catch((err) => {
						console.log(err)
					})
				} else {
					this.$Message.error('');
				}
			})
		},
		getData(){
			let hasGH=cookie.getCookie('hasGH');
			if(hasGH==1){
				this.$router.push({path:'/information'})
			};
			axios.post(`${url}/register/department`)
			.then((result)=>{
				var res=result.data;
				if (res.status==1) {
					this.department=res.result
				}else {
					console.log(res.msg)
				}
			})
			.catch((err) => {
				console.log(err)
			})
		}
	},
	mounted:function(){
		this.getData()
	},
	components:{
		top
	}
}
</script>

<style lang="css" scoped>
.contain{
	width: 100%;
	padding: 30px;
	padding-top: 20px;
	padding-bottom: 0px
}
.con{
	width: 100%;
	height: 60px;
	background-color: #96d3d1;
}
.con .ap{
	color: #ed3f14;
	text-align: center;
	line-height: 60px;
	font-family: ;
	font-size: 18px;
	font-weight: 700
}
</style>


