<template>
	<div class="top">
		<p>
			<Icon type="social-twitter"></Icon>
			
			<Button type="error" v-if="hasGH" shape="circle" @click="login"></Button>
		</p>
	</div>
</template>

<script>
import cookie from './../tools/cookie.js'
import url from './../tools/config.js'
import axios from 'axios'
export default {
	name: 'top',
	data () {
		return {
			hasGH:true,
			value:''
		}
	},
	methods:{
		getData(){
			let Login=cookie.getCookie('hasGH');
			if(Login==1){
				this.hasGH=false
			}
		},
		login(){
			this.$Modal.confirm({
				render: (h) => {
					return h('Input', {
						props: {
							value: this.value,
							autofocus: true,
							placeholder: ''
						},
						on: {
							input: (val) => {
								this.value = val;
							}
						}
					})
				},
				onOk: () => {
					if (this.value!='') {
						axios.post(`${url}/register/get`,{pCardNum:this.value})
						.then((result)=>{
							var res=result.data;
							if (res.status==1) {
								if (res.result.pQueue=='') {
									this.$Message.warning('');
								}else if(res.result.pQueue==''){
									this.$Message.warning('');
								}else{
									cookie.setCookie('hasGH',1,1)
									cookie.setCookie('pCardNum',res.result.pCardNum,1)
									this.$router.push({path:'/information'})
								}
								
							}else {
								this.$Message.warning('');
								console.log(res.msg)
							}
						})
						.catch((err) => {
							this.$Message.warning('');
							console.log(err)
						})
						this.value=''
					}
				},
			})
			
		}
	},
	mounted:function(){
		this.getData()
	}
}
</script>

<style lang="css" scoped>
.top{
	width: 100%;
	height: 60px;
	background-color: #01847f
}
.top p{
	padding-left: 20px;
	line-height: 60px;
	color: #fff;
	font-size: 22px;
	font-family: 
}
.top p button{
	font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","\5FAE\8F6F\96C5\9ED1",Arial,sans-serif;
	float: right;
	margin-top: 15px;
	margin-right: 10px
}
</style>


